// import logo from "./logo.svg";
import "./App.css";
import { useState, useEffect, useRef } from "react";

function App() {
  const [src, setSrc] = useState(null);
  let prekol = "user";
  useEffect(() => {
    navigator.mediaDevices
      .getUserMedia({
        video: {
          facingMode: prekol,
        },
      })
      .then((stream) => {
        setSrc(stream);
      });
  }, [prekol]);
  return (
    <div className="App">
      <video
        width="200px"
        height="200px"
        autoPlay
        muted
        playsInline
        ref={(video) => {
          if (!!src) {
            video.srcObject = src;
          }
        }}
      />
      <button
        onClick={() => {
          prekol = prekol === "user" ? "environment" : "user";
          console.log(prekol);
        }}>
        user
      </button>
    </div>
  );
}

export default App;
